.form__button {
	display: flex;
	justify-content: center;
	margin-top: 25px;
	@media (max-width: 895px) {
		margin-top: 5px;
	}
}

.form__button-style {
	background-color: $primary;
	border-radius: 5px;
	border: none;
	color: white;
	padding: 15px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
}

.form__button-style:hover {
	background-color: darken($color: $primary, $amount: 20);
}
